<template>
<div>U'R get redirect...</div>
<!-- <RouterLink :to="www.google.com">Google</RouterLink> -->
</template>

<script>
import url from "@/components/array/url.json";

export default {
  
  name: 'Redirect',
  data: function() {
    return {
      url
    };
  },
  methods: {
    redirect() {
      const uri = window.location.pathname.replace("/", "")

      url.forEach(u => {
        if (uri == u.name) window.location.href = u.url
      });
    }
  },
  beforeMount(){
    this.redirect()
  },
}
</script>