import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Wiki from "../views/Wiki.vue";
import Redirect from "../components/Redirect.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/v/", component: Home },
  { path: "/v/wiki", component: Wiki },
  { path: "/v/cc", component: Redirect },
  { path: "/v/gg", component: Redirect },
  { path: "/v/dinasti", component: Redirect },
  { path: "/v/btwit", component: Redirect },
  { path: "/v/discord", component: Redirect },
  { path: "/v/trakteer", component: Redirect },
  { path: "/v/donate", component: Redirect },
];

const router = new VueRouter({
  routes,
  mode: "history",
  base: "/v/",
});

export default router;
