
<template>
  <div>
    <div class="main">
      <h2>Dinasti Shitposting Indonesia</h2>

      <div class="menu">
        <div class="items">
          <a
          v-for="menu in menus" :key="menu.id"
            class="item menu-click"
            href="#"
            data-bs-toggle="modal"
            data-bs-target="#menuModal"
            v-on:click="modalLoad(menu.name)"
          >
            {{ menu.icon }}
          </a>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="menuModal"
      tabindex="-1"
      aria-labelledby="menuModalLabel"
      aria-hidden="true"
    >
      <div
        class="
          modal-dialog modal-dialog-centered modal-dialog-scrollable modal-lg
        "
      >
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="menuModalLabel">{{ header }}</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body" id="modal-body"> {{ body }} </div>
          <div class="modal-footer" id="modal-footer">
            <a v-for="button in buttons" :key="button.id"
            :href="button.url" target="_blank" class="btn btn-primary" rel="noopener noreferrer">{{button.name}}</a>
          </div>
        </div>
      </div>
    </div>

    <footer>
      2021 © dinastishitpost
    </footer>

    <div class="fix-alert">
      <div class="alert alert-warning fade show" role="alert">
        <i class="bi bi-exclamation-triangle-fill fs-5"></i>
        <strong style="text-transform: uppercase">Peringatan!!</strong> Kami
        sedang mencari moderator di server discord!!
        <a
          href="https://forms.gle/Bx3cqVHUZzZtikJA7"
          target="_blank"
          rel="noopener noreferrer"
          class="alert-link"
          >Klik disini!</a
        >
        <button
          type="button"
          class="btn-close"
          data-bs-dismiss="alert"
          aria-label="Close"
        ></button>
      </div>
    </div>
  </div>
</template>

<script>
import menus from "@/components/array/main.json";
let btn = [];

export default {
  name: 'Main',
  data: function() {
    return {
      menus,
      buttons: [],
      header: "",
      body: "",
      footer: "",
    };
  },
  methods: {
    modalLoad (name) { 
      menus.forEach(m => {
        if (m.name == name) {
          btn = [];
          console.log(name);

          if (m.modal.button && m.modal.button.length > 0) {
            let id = 0;
            for (const b of m.modal.button) {
              btn.push({id, name: b.name, url: b.url});
              id++;
            }
          }
          
          if (m.modal.title) this.header = m.modal.title;
          if (m.modal.message) this.body = m.modal.message;
          if (m.modal.button && m.modal.button.length > 0) 
            this.buttons = btn;
        }
      });
    }
  },
}
</script>

